@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}html{font-family:'Montserrat', sans-serif;background-color:#f5f5f5}.text-center{text-align:center}.container{max-width:1100px;margin:auto;overflow-x:hidden !important;padding:0 2rem}

.navbar{width:100vw;background-color:#fff8dc;padding:10px;font-size:1rem}.fab{font-size:2rem;padding-right:5px;padding-left:20px}

.flex-wrap{display:flex;flex-wrap:wrap}

.card{width:230px;padding:1rem;margin:.5rem;background-color:#fff8dc;border:1px solid #e7e7e7;box-shadow:10px 10px 5px -4px rgba(0,0,0,0.04)}.round-img{border-radius:50%;height:60px}h3{padding-bottom:10px}.btn{background-color:#2b2b2b;color:#fff;padding:0 6px;font-size:1.7rem;text-decoration:none}.btn:hover{background-color:#575757}

.form-container{margin:30px;display:flex;flex-wrap:wrap;justify-content:center}.text-input{height:40px;width:300px;outline:none;padding:10px;font-weight:400;font-size:1rem;border-radius:50px 0 0 50px;border:1px solid #c4c4c4}.btn-submit{height:40px;outline:none;border:1px solid #c4c4c4;border-radius:0 50px 50px 0;padding:10px;font-weight:700;font-size:1rem;background-color:cornsilk}.btn-submit:hover{background-color:#fff3c5}.btn-clear{font-size:1rem;border:none;color:#474747;padding:10px 10px 10px 20px;background:transparent}

.alert{text-align:center;padding:10px;background-color:#ec7189;width:300px;margin:5px auto 0 auto}

